body {
  margin: 0;
  font-family:'Syne Mono', monospace;
  /* -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale; */
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.header{
  height: 150px;
  font-size: 2rem;
  display: flex;
  justify-content: center;
  align-items: center; 
  font-family: 'Special Elite', cursive;
  color: blue;
  font-weight: 800;
}
.header header{
  rotate: 17deg;
  margin-left: -3.5rem;
  margin-bottom: -4rem;;
}

.header img{
  height: 8rem;
}

.gameboard{
  /* display: grid;

  grid-template-columns: 1fr 1fr 1fr;
  grid-template-rows: repeat(5 1fr); */
  display: grid;
  grid-template-columns: repeat(auto-fill,clamp(150px,30vw,350px));
   /* minmax(350px, 1fr)); */
 grid-column-gap: 6px;
 grid-row-gap:6px;

}
.card{
  display: flex;
  flex-direction: column;
  width: clamp(100px,30vw,350px);
  height: clamp(140px,30vw,400px);
  /* min-width: 350px; */
  /* width: min(90vw,400px);
  height: min(90vw,400px); */
  justify-self: center;
  /* align-self: center; */
  text-align: center;
  align-items: center;
}

.card>img{
  object-fit: cover;
  width: 80%;
  height: clamp(100px,30vw,300px);
  object-position: 50% 10%;
  border: 20px solid rgba(0, 0, 0,.8);
}

.card>div{
  margin-top: -1.2rem;
  color: rgba(255, 255, 255, .9);
  font-weight: 600;
  font-size: clamp(7pt,2vw,12pt);
}

.card>.siderail{
  position: absolute;
  margin-top: 30%;
  margin-left: 85%;
  width: 50px;
  color: rgba(255, 255, 255, .7);
  font-size: clamp(5pt,3vw,10pt);
  transform: rotate(90deg);
   /* translateX(-200%) translateY(-950%); */
  
  
}

.card:nth-child(1),
.card:nth-child(8),
.card:nth-child(13){
  rotate: 1deg;
}

.card:nth-child(3),
.card:nth-child(6),
.card:nth-child(12){
  rotate: -2deg;
}

.card:nth-child(4),
.card:nth-child(14){
  rotate: -.5deg;
}
.card:nth-child(5)
.card:nth-child(7){
  rotate: -.7deg;
}


.card:nth-child(11){
  rotate: .5deg;
}
.card:nth-child(10),
card:nth-child(13){
  rotate: .7deg;
}
#block1{
  margin-top: 70%;
}



#img3{
  rotate: 120deg;
  margin-left: 30%;
}

#img4{
  grid-row: 6;
}
#img5{
  rotate: 20deg;
}
#block6{
  grid-row: 7;
  grid-column: 3;
}

/* #img{
  rotate: 20deg;
} */

#block6{
  grid-row: 4;
  grid-column: 3;
  margin-left: -50%;
  margin-bottom: -60%;
}


/* .cover{
  margin-top: -15vw;
  height: 15vw;
  position: relative;
  z-index: 2;
} */

.display{
  position: fixed;
  top: 4%;
  right: 2%;
  rotate: 2deg;
  background-color: #DD550F;
  /* background-color: rgb(0, 0, 0, .7); */
  color: white;
  padding: 10px;
  /* border-radius: 5px; */
  /* border: 2px solid white; */
  z-index: 2;
  font-weight: 600;
}

.background{
  width: 100vw;
  position: absolute;
  top: 0;
  z-index: -1;
  display: grid;
  gap: 30px;
  grid-template-columns: 1fr 2fr 1fr;
}

.background img{
  height: auto;
  width: min(25vw,4500px);
}

#block1,#block5,#block4{
  height: 30vw;
  width: 25vw;
  background-color: #DD550F;
}
#block3,#block2,#block6{
  background-color: #E48124;
}
#block1,#block6{
  height: max(30vw,300px);
  width: max(25vw,250px);
}
#block1,#block5{
  rotate: 1deg;
}

.win{
  width: 100vw;
  display: flex;
  justify-content: center;
  position: fixed;
  z-index: 3;
}
.win>div div{
  text-align: center;
  margin-top: -3rem;
  color: white;
  font-size: 1.2rem;
  font-weight: 800;
}

footer{
  text-align: center;
  margin-top: 100px;
}



@media only screen and (max-width: 800px) {
  .gameboard{
grid-template-columns: repeat(auto-fill, minmax(120px, 1fr));
gap: 10px;
  }

  .display{
    top: 1%;
  }
  .card>img{
    border: 15px solid rgb(0, 0, 0 , .8);
  }

  .card>div{
    margin-top: -1rem;
  }

  .card .siderail{

    font-size: 6pt;
  }
}

